import React, { useEffect, useState } from 'react';
import Layout from '../components/layout';

export default function Contact() {
  const [name, setName] = useState('');
  const [tel, setTel] = useState('');
  const [email, setEmail] = useState('');
  const [emailConfirm, setEmailConfirm] = useState('');
  const [privacy, setPrivacy] = useState(false);

  const [validEmail, setValidEmail] = useState(false);
  const [valid, setValid] = useState(false);

  const [submitted, setSubmitted] = useState(false);

  useEffect(() => {
    if (email && emailConfirm) {
      setValidEmail(email === emailConfirm);
    } else {
      // 正しくはValidではないがメール欄がどちらか空欄であれば何もしないため
      setValidEmail(true);
    }

    setValid(
      name && tel && email && emailConfirm && email === emailConfirm && privacy
    );
  }, [name, tel, email, emailConfirm, privacy, valid]);

  return (
    <Layout
      page='contact'
      subtitle='お問い合わせ'
      subtitleEn='CONTACT'
      breadcrumbs={[{ link: '/contact/', label: 'お問い合わせ' }]}
    >
      <main className='l-main'>
        <div className='mw_wp_form mw_wp_form_input'>
          <form
            onSubmit={() => {
              setSubmitted(true);
            }}
            action='https://docs.google.com/forms/u/0/d/e/1FAIpQLSecgUEfqYutoHV8lIaDX-da5YIch1aD-Zihj2HIuY9zOg7hvg/formResponse'
          >
            <div className='c-form'>
              <div className='c-form__desc c-form__desc--input'>
                <p className='c-paragraph'>
                  下記のフォームに必要事項を入力の上、「
                  <span className='u-font-bold'>入力内容を確認する</span>
                  」ボタンを押してください。
                </p>
              </div>
              <div className='c-form__desc c-form__desc--confirm'>
                <p className='c-paragraph'>
                  入力内容をご確認の上、「
                  <span className='u-font-bold'>入力内容を送信する</span>
                  」ボタンを押してください。
                </p>
              </div>
              <div className='c-table type-pc-full type-sp-row'>
                <table className='c-table__item'>
                  <colgroup>
                    <col span='1' className='u-size-w030' />
                    <col span='1' className='u-size-w070' />
                  </colgroup>
                  <tbody>
                    <tr>
                      <th>
                        <span className='c-form__req'>お名前</span>
                      </th>
                      <td>
                        <input
                          type='text'
                          name='entry.1058860346'
                          className='c-form__input'
                          size={60}
                          value={name}
                          onChange={e => setName(e.target.value)}
                          placeholder='山田 太郎'
                          required
                        />
                      </td>
                    </tr>
                    <tr>
                      <th>
                        <span className='c-form__req'>性別</span>
                      </th>
                      <td>
                        {['女性', '男性', 'その他'].map((label, i) => (
                          <span key={i} className='horizontal-item'>
                            <label>
                              <input
                                type='radio'
                                name='entry.116201121'
                                defaultValue={label}
                                required
                              />
                              <span>{label}</span>
                            </label>
                          </span>
                        ))}
                      </td>
                    </tr>
                    <tr>
                      <th>
                        <span className='c-form__req'>年齢</span>
                      </th>
                      <td>
                        {[
                          '10代',
                          '20代',
                          '30代',
                          '40代',
                          '50代',
                          '60代以上'
                        ].map((label, i) => (
                          <span key={i} className='horizontal-item'>
                            <label>
                              <input
                                type='radio'
                                name='entry.504960228'
                                defaultValue={label}
                                required
                              />
                              <span>{label}</span>
                            </label>
                          </span>
                        ))}
                      </td>
                    </tr>
                    <tr>
                      <th>
                        <span className='c-form__req'>電話番号</span>
                      </th>
                      <td>
                        <input
                          type='tel'
                          name='entry.1272644312'
                          className='c-form__input'
                          size={60}
                          value={tel}
                          onChange={e => setTel(e.target.value)}
                          placeholder='090-1234-5678'
                          required
                        />
                      </td>
                    </tr>
                    <tr>
                      <th>
                        <span className='c-form__req'>メールアドレス</span>
                      </th>
                      <td>
                        <input
                          type='email'
                          name='emailAddress'
                          className='c-form__input'
                          size={60}
                          value={email}
                          onChange={e => setEmail(e.target.value)}
                          placeholder='example@oxwm.net'
                          required
                        />
                      </td>
                    </tr>
                    <tr>
                      <th>
                        <span className='c-form__req'>
                          メールアドレス（確認用）
                        </span>
                      </th>
                      <td>
                        <input
                          type='email'
                          className='c-form__input'
                          size={60}
                          value={emailConfirm}
                          onChange={e => setEmailConfirm(e.target.value)}
                          placeholder='example@oxwm.net'
                          required
                          style={{
                            borderColor: validEmail ? 'currentcolor' : 'red'
                          }}
                        />
                      </td>
                    </tr>
                    <tr>
                      <th>
                        <span>希望店舗</span>
                      </th>
                      <td>
                        {['上野店', '御徒町店', '東日本橋店', '千駄ヶ谷店'].map(
                          (shop, i) => (
                            <span key={i} className='horizontal-item'>
                              <label>
                                <input
                                  name='entry.601814622'
                                  type='checkbox'
                                  value={shop}
                                />
                                <span>{shop}</span>
                              </label>
                            </span>
                          )
                        )}
                        <p style={{ marginTop: '1em', fontSize: 'small' }}>
                          ※希望店舗が複数ある場合は全てチェックしてください
                        </p>
                      </td>
                    </tr>
                    <tr>
                      <th>
                        <span>お問い合わせ</span>
                      </th>
                      <td>
                        <textarea
                          name='entry.125943433'
                          className='c-form__textarea'
                          cols={40}
                          rows={10}
                          placeholder='お問い合わせ内容、ご質問をご記載ください。カウンセリングの希望日程がある場合は第三希望日時までご記載ください。'
                        ></textarea>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div className='c-form-agreement'>
                <div className='c-form-agreement__inner'>
                  <p>
                    当ジムの「
                    <a
                      href='../privacy/'
                      target='_blank'
                      className='c-link'
                      rel='noopener noreferrer'
                    >
                      <span className='i-blank'>プライバシーポリシー</span>
                    </a>
                    」に同意いただける方は、チェックボックスにチェックを入れてください。
                  </p>
                  <span className='mwform-checkbox-field horizontal-item'>
                    <label>
                      <input
                        type='checkbox'
                        defaultValue='プライバシーポリシーに同意する'
                        checked={privacy}
                        onChange={e => setPrivacy(e.target.checked)}
                        required
                      />
                      <span className='mwform-checkbox-field-text'>
                        プライバシーポリシーに同意する
                      </span>
                    </label>
                  </span>
                </div>
              </div>
              <div className='c-form-buttons'>
                <div className='c-form__submit'>
                  <input
                    type='submit'
                    defaultValue='送信'
                    disabled={!valid || submitted}
                  />
                </div>
                <div className='c-form__back'></div>
              </div>
            </div>
          </form>
        </div>
      </main>
    </Layout>
  );
}
